<template>
    <h1>Grammar</h1>
    <div class="nav">

        <button v-for="button in buttons"
                @click="isVisible = button"
                :disabled="isVisible === button">
            {{ button }}
        </button>
    </div>

    <present-simple :description="presentSimple" v-if="isVisible === 'Present Simple'"></present-simple>
    <past-simple :description="pastSimple" v-if="isVisible === 'Past Simple'"></past-simple>
    <present-continuous v-if="isVisible === 'Present Continuous'"></present-continuous>
    <past-continuous v-if="isVisible === 'Past Continuous'" :examples="pastContinuous"></past-continuous>
    <future-simple v-if="isVisible === 'Future Simple'"></future-simple>
    <present-perfect v-if="isVisible === 'Present Perfect'" :examples="presentPerfect"></present-perfect>
    <past-perfect v-if="isVisible === 'Past Perfect'" :examples="pastPerfect"></past-perfect>
    <present-perfect-continuous v-if="isVisible === 'Present Perfect Continuous'" :examples="presentPerfectContinuous"></present-perfect-continuous>
    <past-perfect-continuous v-if="isVisible === 'Past Perfect Continuous'" :examples="pastPerfectContinuous"></past-perfect-continuous>


</template>

<script>
    import PresentSimple from "../components/Tenses/PresentSimple";
    import PastSimple from "../components/Tenses/PastSimple";
    import PresentContinuous from "../components/Tenses/PresentContinuous";
    import FutureSimple from "../components/Tenses/FutureSimple";
    import PresentPerfect from "../components/Tenses/PresentPerfect";
    import PastPerfect from "../components/Tenses/PastPerfect";
    import PastContinuous from "@/components/Tenses/PastContinuous";
    import PresentPerfectContinuous from "@/components/Tenses/PresentPerfectContinuous";
    import PastPerfectContinuous from "@/components/Tenses/PastPerfectContinuous";
    export default {
        name: "GrammarPage",
        components: {
            PastPerfectContinuous,
            PresentPerfectContinuous, PastContinuous,
            PastPerfect, PresentPerfect, FutureSimple, PresentContinuous, PastSimple, PresentSimple},
        data() {
            return {
                isVisible: 'Present Simple',
                buttons: [
                    'Present Simple', 'Past Simple', 'Future Simple', 'Present Continuous', 'Past Continuous', 'Present Perfect',
                    'Present Perfect Continuous', 'Past Perfect', 'Past Perfect Continuous'
                ],

                presentSimple: '',
                pastSimple: '',
                presentPerfect: {
                    active: [
                        'Tom has already brought the package.',
                        'They have not planted the trees yet.',
                        'I haven’t bought the new car. This is my old one',
                        'Have we bought all the furniture?'
                    ],
                    passive: [
                        'The package has already been brought.',
                        'The trees have not been planted yet.'
                    ],
                    description: '<b>Present Perfect</b> використовується для передачі важливості результату дії для цього без точної вказівки часу. Дія відбулася колись у минулому і результат цієї дії (або її відсутність) видно у теперішньому.<br/> <small>Examples: </small> <p>- I’ve done this job well.</p><p>- She has found this kitten outside.</p><p>- We haven’t bought new chairs yet. Our kitchen is empty.</p><br/><b>Present Perfect</b> використовується для опису дії, яка почалася колись у минулому, неважливо, коли, але ще не закінчилося, і тепер видно результат цієї дії.<br/><small>Example:</small><p>- They have played in the orchestra since high school. </p>'
                },
                pastContinuous: {
                    active: [
                        'Tom was writing a letter at 5 p.m. yesterday.',
                        'I was reading a book.',
                        'Was Tom writing a letter at 5 p.m.?',
                        'This expensive hotel was holding a wedding party on that day.',
                        'At midnight I was driving home ',
                        ' If it were not raining, we would have a BBQ'
                    ],
                    passive: [
                        'The letter was being written at 5 p.m. yesterday.',
                        'A book was being read',
                        'Was the letter being written at 5 p.m. yesterday?',
                        'On that day a wedding party was being held at this expensive hotel.'
                    ],
                    description: '<b>Past Continuous</b> використовується для опису дій, що відбувалися у конкретний, точно відомий момент часу або деякий проміжок часу у минулому.<br/> <small>Example: </small> <p>- At 5 o’clock yesterday evening my sister was flying from Sydney.</p><br/>Точний момент часу в <b>Past Continuous</b> може уточнюватися або перериватись іншою, більш короткочасною дією, яка виражена в <b>Past Simple</b>.<br/><small>Example: </small> <p>- She was cleaning the house when the phone rang.</p><br/> Слова <b>while</b> та <b>when</b> підкреслюють різні частини речення. Як правило, після слова while слідує <b>Past Continuous</b>, а за словом when слідує <b>Past Simple</b>.<br/><small>Examples:</small><p>- I was eating my soup when he came home.</p><p>- While I was typing my report everyone left the office.</p><br/><b>Past Continuous</b> використовується для опису кількох дій в одному реченні, які відбувалися одночасно.<br/><small>Examples:</small><p>- They were drinking a cup of coffee while they were sitting at that restaurant.</p><p>- I was playing the guitar and Jane was playing along the drum.</p><br/><b>Past Continuous</b> використовується для вираження несхвалення, нетерплячості або роздратування до будь-яких дій у минулому. У цьому сенсі <b>Past Continuous</b> зазвичай використовується зі словами <b>always</b>, <b>often</b>, <b>constantly</b>. Ці слова ставляться між допоміжним дієсловом <b>was</b> чи <b>were</b> і смисловим дієсловом із закінченням <b>-ing</b>.<br/><small>Examples:</small><p>- He was always interrupting me when I was talking.</p><p>- We were often waiting for that friend of his. He was so slow.</p><br/><b>Past Continuous</b> використовується для опису запланованих дій у минулому, які не відбулися. Зазвичай у таких реченнях використовуються дієслова <b>mean</b>, <b>intend</b>, <b>hope</b>, <b>expect</b>, <b>think</b>, <b>plan</b>.<br/><small>Example:</small><p>- I was hoping to meet her at the bookstore but she didn’t come. </p>',
                },
                pastPerfect: {
                    active: [
                        'Tom had brought the package before you came.',
                        'I saw that everybody had already finished',
                        'Had the plane arrived by that time?',
                    ],
                    passive: [
                        'The package had been brought before you came.',
                        'The book that was given to me had been read by my sister.',

                    ],
                    description: '<b>Past Perfect</b> використовується для вираження дії, яка передувала іншій дії в минулому або певному моменту в минулому.<br/> <small>Examples: </small> <p>- Tom had drunk a cup of coffee before going to work.</p><p>- They had finished their work by 6 p.m.</p><br/><b>Past Perfect</b> використовується для вираження дій, які завершилися у минулому і чий результат було видно у минулому.<br/><small>Examples:</small><p>- She was upset. She had torn her favorite dress.</p><p>- All these people were happy, because Kennedy had won the elections.</p><br/><b>Past Perfect</b> використовується в конструкціях <b>hardly...when</b> і <b>no sooner...than</b>. Обидва вирази перекладаються як <b>"ледь я"</b>, <b>"як тільки я"</b>, <b>"не встиг я"</b>. У цих випадках <b>Past Perfect</b> теж описує минулу дію, яка сталася перед іншою дією у минулому. Але особливістю тут є те, що порядок слів у тій частині, де вживається <b>Past Perfect</b>, такий самий, як у загальному питанні. Це називається інверсією. У другій половині речення використовується час <b>Past Simple</b>.<br/><small>Examples:</small><p>- Hardly had they met each other when it started to drizzle.</p><p>- Hardly had I sent my message when she came.</p><p>- No sooner had I come into the flat than the telephone rang.</p>'
                },
                presentPerfectContinuous: {
                    active: [
                        'I have been working for an hour.',
                        'I have been watching this trick all this time. ',
                        'How long has she been waiting for you?',
                        'Ann hasn’t been living in this house for 10 years.',
                        'They have not been travelling.',
                        'I have been going to this gym since I moved to this neigbourhood a year ago'
                    ],
                    description: '<b>Present Perfect Continuous</b> використовується для вираження дії, яка почалася в минулому і відбувалася до цього моменту або, можливо, все ще відбувається. Якщо дію завершено, його результат видимий у теперішньому. У цьому часі наголошується саме на тривалості дії і дається відповідь на питання <b>«як довго відбувається дія?»</b>.<br/> <small>Examples: </small> <p>- I’ve been standing here for two hours.</p><p>- She’s been running for an hour, she feels tired.</p><br/><b>Present Perfect Continuous</b> використовується з приводом часу <b>since</b>, де після <b>since</b> може використовуватися для відліку часу конкретна дата або інша подія в минулому, яка виражається в <b>Past Simple</b>.<br/><small>Example:</small><p>- I’ve been working here since I finished the university. </p><br/><b>Present Perfect Continuous</b> використовується для вираження агресії, невдоволення та роздратування.<br/><small>Examples:</small><p>- Someone has been rummaging in my flat. I can’t find my gold watch. </p><p>- He has been joking like an idiot all day. I hate his sense of humor.</p>'
                },
                pastPerfectContinuous: {
                    active: [
                        'I had been working for an hour when he came.',
                        'She hadn’t been driving a car for an hour before she came to the gas station.',
                        'John had been speaking all morning, so he lost his voice',
                        'He had been running for two hours before she saw a village.',
                        'Не had been watching TV for several hours when his mother told him to go to bed.',
                        'Of course she wasn’t tired! He had not been working like a horse all the week.'
                    ],
                    description: '<b>Past Perfect Continuous</b> використовується для того, щоб підкреслити саме тривалість, тривалість дії, яка розпочнеться та закінчиться до якогось моменту чи іншої дії у минулому. Результат цієї дії також видимий у минулому.<br/> <small>Examples: </small> <p>- John had been speaking all morning, so he lost his voice.</p><p>- They had been sitting here since last evening before they bought the tickets.</p>'
                }
            }
        }
    }
</script>

<style scoped>
    .nav {
        text-align: center;
        margin: 50px;
    }
    .nav button {
        font-size: 16px;
        padding: 5px;
        margin: 1px;
        cursor: pointer;
        font-family: Montserrat, sans-serif;
        font-weight: 500;
    }


</style>