        </main-button>
    </div>
    <form class="search" @submit.stop>
        <input type="text" v-model="search" placeholder="Search">
        <main-button type="reset" class="btn" @click="search = ''">Clear</main-button>
    </form>
    <table class="irregularTable" v-if="irregularVerbsStatus">
        <thead>
            <tr>
                <th>Usage</th>
                <th>Infinitive</th>
                <th title="Past Simple">Past Simple</th>
                <th title="Past Participle">Past Participle</th>
                <th>Translation</th>
            </tr>
        </thead>
        <tbody>
            <template v-if="search && sortedWords.length">
               <tr>
                   <td class="br-none"></td>
                   <td class="br-none">Searched</td>
                   <td colspan="4"></td>
               </tr>
                <tr class="searched" v-for="item in sortedWords">
                    <td class="rate">
                        <span :style="`width:${item.rate}px`"></span>
                    </td>
                    <td class="pointer" :title="item['1'].tc" @click="speechWord(item['1'].text)">
                        {{ item['1']?.text }}
                    </td>
                    <td class="pointer" :title="item['2'].tc" @click="speechWord(item['2'].text)">
                        {{ item['2']?.text }}
                    </td>
                    <td class="pointer" :title="item['3'].tc" @click="speechWord(item['3'].text)">
                        {{ item['3']?.text }}
                    </td>
                    <td style="max-width: 550px">{{ item['tl'] }}</td>
                </tr>
            </template>
            <tr v-for="item in tableArr">
                <template v-if="item?.rate">
                    <td class="rate">
                        <span :style="`width:${item.rate}px`"></span>
                    </td>
                    <td class="pointer" :title="item['1'].tc" @click="speechWord(item['1'].text)">
                        {{ item['1']?.text }}
                    </td>
                    <td class="pointer" :title="item['2'].tc" @click="speechWord(item['2'].text)">
                        {{ item['2']?.text }}
                    </td>
                    <td class="pointer" :title="item['3'].tc" @click="speechWord(item['3'].text)">
                        {{ item['3']?.text }}
                    </td>
                    <td style="max-width: 550px">{{ item['tl'] }}</td>
                </template>
                <template v-else>
                    <td class="br-none"></td>
                    <td class="br-none">{{ item?.letter }}</td>
                    <td colspan="4"></td>
                </template>
            </tr>
        </tbody>
    </table>

    <table class="modalTable"  v-if="modalVerbsStatus">
        <thead>
        <tr>
            <th><b>Modal</b></th>
            <th><b>USES</b></th>
            <th><b>Present / Future </b></th>
            <th> <b>PAST </b></th>
        </tr>
        </thead>
        <tbody>
        <tr>
            <td>be supposed to</td>
            <td>expectation</td>
            <td>We <b>are supposed to</b> meet them here.</td>
            <td>We <b>were supposed to</b> meet here.</td>
        </tr>
        <tr>
            <td>be to </td>
            <td>strong expectation</td>
            <td>We <b>are to</b> meet them here.</td>
            <td> We <b>were to</b> meet them here.</td>
        </tr>
        <tr>
            <td>
                can / could<br>
                Can - Can't<br>
                Could
            </td>
            <td>
                ability / possibility<br>
                informal permission <br>
                informal polite request <br>
                impossibility (negative only)
            </td>
            <td>
                I <b>can</b> learn modal verbs online.<br>
                You <b>can</b> use my car tomorrow. <br>
                <b>Can</b> I borrow your book?<br>
                Cats <b>can't</b> swim.
            </td>
            <td>
                I <b>could</b> jump high a few years ago but now I <b>can't</b>.<br>
                That <b>can't</b> have been true!</td>
        </tr>
        <tr>
            <td>have to</td>
            <td>necessity <br>lack of necessity (negative)</td>
            <td>
                I <b>have to</b> go to class today.<br>
                I <b>don't have to</b> go to class today.
            </td>
