            <td>had better</td>
            <td>strongly advised</td>
            <td>You <b>had better</b> be on time.</td>
            <td> (past form uncommon) </td>
        </tr>
        <tr>
            <td> may </td>
            <td> polite request <br>formal permission <br> less than 50% certainty </td>
            <td>
                <b>May</b> I borrow your book? <br>
                You <b>may</b> leave the room. <br>
                He <b>may</b> be at the school.<br>
            </td>
            <td> He <b>may</b> have been at school.</td>
        </tr>
        <tr>
            <td>Might</td>
            <td>less than 50% certainty </td>
            <td>He <b>might</b> be at school.</td>
            <td>He <b>might have been</b> at school. </td>
        </tr>
        <tr>
            <td>must</td>
            <td>
                strong necessity <br>
                prohibition<br>
                95% certainty
            </td>
            <td>
                I <b>must</b> go to class today.<br>
                You <b>must not</b> open that door.<br>
                Mary isn't in class. She <b>must</b> <u>be</u> sick.(present only)
            </td>
            <td>
                I <b>had to</b> go to class yesterday.<br>
                Mary <b>must</b> <u>have been</u> sick yesterday.
            </td>
        </tr>
        <tr>
            <td>ought to</td>
            <td>
                advisability<br>
                80% certainty
            </td>
            <td>
                I <b>ought to</b> study tonight.<br>
                She <b>ought to</b> be at school
            </td>
            <td>
                I <b>ought to</b> have studied last night.<br>
                She <b>ought to</b> have done well on the test.
            </td>
        </tr>
        <tr>
            <td>shall</td>
            <td>ask another person's opinion (only used with I or we)</td>
            <td>
                <b>Shall</b> I invite them too? <br>
                Shall we dance?
            </td>
            <td></td>
        </tr>
        <tr>
            <td>should</td>
            <td>
                advisability <br>
                80% certainty<br>
            </td>
            <td>
                I <b>should</b> study tonight <br>
                He <b>should</b> be at school<br>
            </td>
            <td>
                You <b>should</b> have paid your bills. <br>
                He <b>should</b> have done well on the test.
            </td>
        </tr>
        </tbody>
    </table>
    <main-button class="verb-test-btn" @click="modalVisible = !modalVisible">
        Check yourself
    </main-button>
    <ModalWindow v-model:show="modalVisible">
        <div class="verb-test">
            <div style="cursor: pointer" @click="[show1 = true, show2 = true, show3 = true]">{{ arrayRandElement.tl }}</div>
            <div>
                <div @click="[show1 = true, speechWord(arrayRandElement['1'].text)]" :class="{'hide': !show1}">{{ arrayRandElement['1'].text }}</div>
                <hr>
            </div>
            <div>
                <div @click="[show2 = true, speechWord(arrayRandElement['2'].text)]" :class="{'hide': !show2}">{{ arrayRandElement['2'].text }}</div>
                <hr>
            </div>
            <div>
                <div @click="[show3 = true, speechWord(arrayRandElement['3'].text)]" :class="{'hide': !show3}">{{ arrayRandElement['3'].text }}</div>
                <hr>
            </div>
        </div>
        <div style="text-align: center; margin-top: 30px;">
            <main-button @click="randomItem">
                Next
            </main-button>
        </div>
    </ModalWindow>
</template>

<script>
import {verbsStore} from "@/store/verbs";
import TempComponent from "@/views/TempComponent";
    export default {
        name: "IrregularVerbs",
        components: {TempComponent},
        data() {
            return {
                modalVisible: false,
                irregularVerbsStatus: true,
                modalVerbsStatus: false,
                tableArr: [],
                search: '',
                random: 0,
                show1: false,
                show2: false,
                show3: false,
                name: '',
                selectedVoice: 8,
                synth: window.speechSynthesis,
                voiceList: [],
                wordSpeech: new window.SpeechSynthesisUtterance()
            }
        },
        methods: {
            randomItem() {
                this.random = Math.floor(Math.random() * this.tableArr.length);
                this.show1 = false;
                this.show2 = false;
                this.show3 = false;
            },

            speechWord (val) {
                this.wordSpeech.text = val;

                this.wordSpeech.voice = this.voiceList[this.selectedVoice]
                this.wordSpeech.volume = 1;

                if (this.synth.speaking) this.synth.cancel();
                if (this.synth.pending) {
                    this.synth.cancel()
                } else this.synth.speak(this.wordSpeech)
            }
        },
        computed: {
            arrayRandElement() {
                return this.tableArr[this.random];
            },
            sortedWords(){
                return [...this.tableArr].filter(item =>
                    this.search.match(/^[а-яА-Я]+$/) ? item?.tl.toLowerCase().includes(this.search) :
                        item['1']?.text.toLowerCase().includes(this.search) || item['2']?.text.toLowerCase().includes(this.search) || item['3']?.text.toLowerCase().includes(this.search)
                );
            }
        },
        mounted() {
            this.tableArr = verbsStore().table;
            this.voiceList = this.synth.getVoices()

            this.synth.onvoiceschanged = () => {
                this.voiceList = this.synth.getVoices()
                //0 - 23 voices
            }
        },
        watch: {
            modalVisible() {
                this.randomItem();
            }
        }
    }
</script>

