export default {
    name: "TempComponent",
    data() {
        return {
            items: [],
        }
    },
    methods: {
        getWords() {
            const tbody = this.$refs.tbody;
            tbody.querySelectorAll('tr').forEach((el, index) => {
                let word = {
                    rate: '',
                    '1' : {
                        text: '',
                        tc: '',
                    },
                    '2' : {
                        text: '',
                        tc: '',
                    },
                    '3' : {
                        text: '',
                        tc: '',
                    },
                    tl: ''
                }

                const letter = {
                    letter: '',
                }
                if(el.classList.contains('capital-letter-row')) {
                    letter.letter = el.querySelector('.menu-alphabet').innerText;
                    this.items.push(letter);
                }
                if(el.classList.contains('js-table-row')) {
                    const en = el.querySelectorAll('td');
                    word['1'].text = en[1].querySelector('p').innerText;
                    word['1'].tc = en[1].querySelector('span').innerText;
                    word['2'].text = en[2].querySelector('p').innerText;
                    word['2'].tc = en[2].querySelector('span').innerText;
                    word['3'].text = en[3].querySelector('p').innerText;
                    word['3'].tc = en[3].querySelector('span').innerText;
                    if(en[4].classList.contains('object-for-hint')) {
                        word.tl = en[4].querySelector('.description-wrap').innerText;
                    } else {
                        word.tl = en[4].innerText;
                    }
                    word.rate = en[0].querySelector('.frequency-marker').offsetWidth;
                    this.items.push(word);
                }

            })
        }
    },
    mounted() {
        this.getWords();
    }
}
